/* CheckBox Styling */
.fontStyle {
  font-family: Segoe, "Segoe UI", "DejaVu Sans", "Trebuchet MS", Verdana,
    sans-serif;
  font-size: 13px;
}
.custom-checkbox-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
  min-height: 2rem;
  min-width: 32%;
}

.custom-checkbox {
  appearance: none;
  -webkit-appearance: none;
  min-width: 20px;
  min-height: 20px;
  border: 2px solid #ccc;
  border-radius: 0px;
  position: relative;
  cursor: pointer;
  background-color: white;
  transition: background-color 0.2s;
}

.custom-checkbox:checked {
  background-color: #9cc2cb; /*  custom fill color */
  border-color: #9cc2cb;
}

.custom-checkbox:checked::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 6px;
  min-width: 5px; /* Tick size */
  min-height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.custom-radio-wrapper {
  display: flex;
  align-items: flex-end;
  gap: 8px;
  min-height: 2rem;
  font-size: small;
}

.custom-radio {
  appearance: none;
  -webkit-appearance: none;
  min-width: 20px;
  min-height: 20px;
  border: 2px solid #ccc;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  background-color: white;
  transition: background-color 0.2s, border-color 0.2s;
}

.custom-radio:checked {
  background-color: #9cc2cb; /* Fill color when selected */
  border-color: #9cc2cb;
}

.custom-radio:checked::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 6px;
  min-width: 5px;
  min-height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg); /* Tick mark */
}

.custom-checkbox-white:checked {
  background-color: white; /*  custom fill color */
  border-color: rgb(112, 112, 112);
}

.custom-checkbox-white:checked::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 4px;
  min-width: 8px; /* Tick size */
  min-height: 12px;
  border: solid black;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.checkboxText {
